#root {
  height: 100%;
}
.App {
  height: 100%;
  transition: 30ms;
}
.option-enter {
  opacity: 0;
}
.option-enter.option-enter-active {
  opacity: 1;
  transform: translateX(100px);
}
.option-exit {
  opacity: 1;
}
.option-exit.option-exit-active {
  opacity: 0;
  transform: translateX(100px);
}
button[data-direction] {
  top: calc(85% - 17px);
  right: 44%;
  width: 90px;
  height: 50px;
  display: block;
  cursor: pointer;
  border: none;
  position: absolute;
  background: url("./assets/img/icons/Arrow3.svg") no-repeat center;
}

button[data-direction="left"] {
  /* Left button */

  transform: rotateZ(180deg);
  right: 50%;
}
button[data-direction="right"] {
  /* Right button */
}
