.wrap {
  height: 100vh;
  background-color: #0c0c0c;
  color: #fff;
  font-family: Roboto Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 80px;
  line-height: 106px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.text::after {
  content: "|";
  display: inline;
  -webkit-animation: blink 0.7s infinite;
  -moz-animation: blink 0.7s infinite;
  animation: blink 0.7s infinite;
}
.typed-cursor {
  opacity: 0;
  display: none;
}
.banner {
  width: 100%;
  position: relative;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.content {
  width: 1200px;
  height: 280px;
}
.type__wrap {
  margin-bottom: 87px;
}
.btn__container {
  width: 1200px;
}
.block {
  position: relative;
  height: 100%;
}
.wrap {
  position: relative;
  z-index: 4;
}
.works__wrapper {
  position: relative;
  z-index: 2;
}
